<template>
  <div>
    <div v-if="$store.state.isLogined">
      <div>
        <div class="row bg border-left">
          <div class="col-1">
            标题
          </div>
          <div class="col-11">
            <b-form-input v-model="article.Title" type="text"></b-form-input>
          </div>
        </div>
      </div>
      <mavon-editor ref="me" v-model="article.Content" :toolbars="toolbars" :boxShadow="false" placeholder="" />
      <div class="clear"></div>
    </div>
    <div v-if="!$store.state.isLogined">
      <div class="bg head border-left">
        <h2>{{article.Title}}</h2>
      </div>
      <mavon-editor ref="me" v-model="article.Content" :toolbarsFlag="false" :editable="false" :subfield="false" defaultOpen="preview" :toolbars="toolbars" :boxShadow="false" placeholder="" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Article',
  data () {
    return {
      timeOut: null,
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        mark: true, // 标记
        superscript: true, // 上角标
        quote: true, // 引用
        ol: true, // 有序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        help: true, // 帮助
        code: true, // code
        subfield: true, // 是否需要分栏
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        /* 1.3.5 */
        undo: true, // 上一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: false // 导航目录
      },
      article: Object
    }
  },
  methods: {
    loadDetail (article) {
      this.article = article
      let that = this
      this.$axios.post(this.$API.ART_DETAIL + '?id=' + this.article.ID).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.$set(that.article, 'Content', response.Data.Content)
          that.article.Content = response.Data.Content
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    },
    removeDetail () {
      this.article = Object
    }
  },
  watch: {
    article: {
      handler () {
        if (this.timeOut) {
          clearTimeout(this.timeOut)
          this.timeOut = null
        }

        if (this.$store.state.isLogined) {
          let that = this
          this.timeOut = setTimeout(function () {
            that.$axios.post(that.$API.ART_UPDATE, JSON.stringify(that.article)).then(response => {
              if (that.$response.Code.Success === response.Code) {

              } else {
                that.errorMessage = response.Message
              }
            }).catch(err => {
              that.errorMessage = err.Error
            })
          }, 1000)
        }
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .bg {
    background-color: #fafafa;
  }
  .head {
    padding: 1rem;
  }
  .row {
    margin: 0;
  }
  .row .col-1 {
    line-height: 2.5rem;
  }
  .row .col-1, .row .col-11 {
    padding: 1rem 0;
  }
</style>



