<template>
  <div class="main-container">
    <div class="right-innter-container">
      <div class="row">
        <b-col offset="2" cols="8" class="custom">
        <div>
          <h2>{{article.Title}}</h2>
        </div>
        <mavon-editor ref="me" v-model="article.Content" :toolbarsFlag="false" :editable="false" :subfield="false" defaultOpen="preview" :toolbars="toolbars" :boxShadow="false" placeholder="" />
        </b-col>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'ArticleDetail',
  props: {
    article: Object
  },
  mounted () {
    this.loadDetail()
  },
  methods: {
    loadDetail () {
      let that = this
      this.$axios.post(this.$API.ART_DETAIL + '?id=' + this.$route.params.id).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.article = response.Data
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-innter-container {
    padding: 3rem 0;
  }

  .custom {
    padding: 1rem;
    border: 1px solid #eee;
    border-radius: 1rem;
    box-shadow: 0px 0px 20px #fff;
    background-color: #fff;
  }
</style>



