<template>
  <div class="main-container">
    <div class="right-innter-container">
      <div class="row">
        <b-col offset="2" cols="8">
          <b-list-group>
            <b-list-group-item :href="'/art/' + art.ID" v-for="(art, index) in articleList" :key="index">
              <div>
                {{art.Title}}
              </div>
            </b-list-group-item>
          </b-list-group>
        </b-col>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      articleList: [],
      requestObject: {
        Page: 1,
        Size: 10
      }
    }
  },
  computed: {
    allDirs () {
      return this.$Util.getDir(this)
    }
  },
  mounted () {
    let that = this
    this.$axios.post(this.$API.ART_PAGE, JSON.stringify(this.requestObject)).then(response => {
      if (that.$response.Code.Success === response.Code) {
        that.articleList = response.Data.Content
      } else {
        that.errorMessage = response.Message
      }
    }).catch(err => {
      that.errorMessage = err.Error
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-innter-container {
    padding: 5rem 0;
  }
</style>



